.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #84d9bc;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
  line-height: 2;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1,
h2,
h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}

ul {
  padding: 0;
  list-style: none;
}

.seccion {
  padding: 3rem 0;
}
@media (min-width: 1024px) {
  .seccion {
    padding: 8rem 0;
  }
}

.boton__primario {
  background-color: #84d9bc;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #313c4e;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.boton__primario:hover {
  cursor: pointer;
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .boton__primario {
    width: auto;
  }
}

.catalogo__boton {
  background-color: #84d9bc;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #313c4e;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  border: 2px solid #FFFFFF;
  margin-top: 2rem;
  display: block;
}
.catalogo__boton:hover {
  cursor: pointer;
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .catalogo__boton {
    width: auto;
  }
}

.header {
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url("../../../build/img/header.webp");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
  padding: 5rem 0;
}
.header__logo {
  max-width: 20rem;
  margin: 0 auto;
}
@media (min-width: 1024px) {
  .header__logo {
    margin: 0;
  }
}
.header__contenido {
  padding: 10rem 0;
}
@media (min-width: 1024px) {
  .header__contenido {
    padding: 15rem 0;
  }
}
.header__texto {
  color: #FFFFFF;
  font-size: 4.5rem;
}
@media (min-width: 1024px) {
  .header__texto {
    font-size: 6rem;
    max-width: 40%;
  }
}

@media (min-width: 1024px) {
  .seccion__creaGrid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.seccion h2 {
  font-size: 3.6rem;
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
@media (min-width: 1024px) {
  .seccion h2 {
    margin-top: 0;
  }
}
.seccion h2::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.5rem 0 0 0;
}

.beneficios {
  background-color: #84d9bc;
  padding: 3rem 0;
}
@media (min-width: 1024px) {
  .beneficios {
    padding: 8rem 0;
  }
}
@media (min-width: 1024px) {
  .beneficios__grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(5, 1fr);
    align-items: center;
  }
}
.beneficios__contenido {
  grid-column: 1/4;
}
.beneficios__heading {
  font-size: 3.6rem;
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
@media (min-width: 1024px) {
  .beneficios__heading {
    margin-top: 0;
  }
}
.beneficios__heading::after {
  content: "";
  background-color: #FFFFFF;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.5rem 0 0 0;
}
.beneficios__imagen {
  grid-column: 4/6;
  transition: transform 0.3s;
}
.beneficios__imagen:hover {
  transform: rotate(8deg) scale(1.1);
}

.estadisticas {
  padding: 3rem 0;
}
@media (min-width: 1024px) {
  .estadisticas {
    padding: 8rem 0;
  }
}
.estadisticas-heading {
  font-size: 3.6rem;
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
@media (min-width: 1024px) {
  .estadisticas-heading {
    margin-top: 0;
  }
}
.estadisticas-heading::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.5rem 0 0 0;
}

.planes {
  padding: 3rem 0;
  background-color: #313c4e;
}
.planes__heading {
  font-size: 3.6rem;
  display: inline-block;
  margin-top: 5rem;
  color: #FFFFFF;
}
@media (min-width: 1024px) {
  .planes__heading {
    margin-top: 0;
  }
}
.planes__heading::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.5rem 0 0 0;
}
@media (min-width: 1024px) {
  .planes {
    padding: 8rem 0;
  }
}
.planes__descripcion {
  color: #FFFFFF;
}

.catalogo {
  display: flex;
  flex-direction: column;
  background-color: #FFFFFF;
}
@media (min-width: 1024px) {
  .catalogo {
    flex-direction: row;
    max-width: 80rem;
    /* margin: 10rem auto 0 auto; */
    margin-inline: auto;
    /*margin-inline: margen a los lados */
    margin-block-start: 10rem;
    /*margin-block: margin arriba y abajo */
    /*margin-block-start: margin arriba */
  }
}
.catalogo__inicio {
  flex-grow: 1;
  padding: 16rem 4rem 4rem 4rem;
  border-bottom: 1px solid #e1e1e1;
  background-image: url("/build/img/icono_inicio.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
@media (min-width: 1024px) {
  .catalogo__inicio {
    border-bottom: none;
    transition: transform 0.3s;
  }
  .catalogo__inicio:hover {
    transform: scale(1.1);
  }
}
.catalogo__negocio {
  flex-grow: 1;
  padding: 21rem 4rem 4rem 4rem;
  background-color: #84d9bc;
  border-bottom: 1px solid #e1e1e1;
  background-image: url("/build/img/icono_negocio.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
  order: -1;
}
@media (min-width: 1024px) {
  .catalogo__negocio {
    order: unset;
    margin-block: -5rem;
    border-radius: 2rem;
    background-position: 50% 7rem;
    transition: transform 0.3s;
    z-index: 1;
  }
  .catalogo__negocio:hover {
    transform: scale(1.1);
  }
}
.catalogo__pro {
  flex-grow: 1;
  padding: 16rem 4rem 4rem 4rem;
  background-image: url("/build/img/icono_pro.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
@media (min-width: 1024px) {
  .catalogo__pro {
    transition: transform 0.3s;
  }
  .catalogo__pro:hover {
    transform: scale(1.1);
  }
}
.catalogo__precio {
  font-size: 1.8rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
}
.catalogo__listado {
  text-align: center;
}
.catalogo li {
  margin-bottom: 2rem;
}
.catalogo li:last-of-type {
  margin-bottom: 0;
}

.testimoniales {
  padding: 5rem 0;
}
@media (min-width: 1024px) {
  .testimoniales {
    padding: 8rem 0;
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(5, 1fr);
    align-items: center;
  }
}
.testimoniales__imagen {
  display: flex;
  justify-content: center;
}
@media (min-width: 1024px) {
  .testimoniales__imagen {
    /*Css code*/
    grid-column: 1/3;
  }
}
.testimoniales__contenido {
  grid-column: 3/6;
}
.testimoniales__heading {
  font-size: 3.6rem;
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
@media (min-width: 1024px) {
  .testimoniales__heading {
    margin-top: 0;
  }
}
.testimoniales__heading::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.5rem 0 0 0;
}
.testimoniales__opinions {
  background-color: #84d9bc;
  border: 0.5rem solid #313c4e;
  margin-bottom: 4rem;
  padding: 2rem;
  border-radius: 1rem;
  position: relative;
}
@media (min-width: 1024px) {
  .testimoniales__opinions {
    max-width: 70%;
  }
  .testimoniales__opinions:nth-child(2) {
    margin-left: 10%;
  }
  .testimoniales__opinions:nth-child(3) {
    margin-left: 20%;
  }
  .testimoniales__opinions:nth-child(4) {
    margin-left: 30%;
  }
}
.testimoniales__opinions::before {
  content: "";
  display: block;
  width: 5rem;
  height: 5rem;
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
}
.testimoniales__opinions:last-of-type {
  margin-bottom: 0;
}
.testimoniales__texto {
  margin: 0;
  padding-left: 6rem;
}

.footer {
  background-color: #313c4e;
  color: #FFFFFF;
}
.footer__grid {
  padding: 5rem 0;
}
@media (min-width: 1024px) {
  .footer__grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer__widget {
  margin-bottom: 2rem;
}
.footer__widget:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 1024px) {
  .footer__widget {
    margin-bottom: 0;
  }
}
.footer__link {
  display: block;
  color: #FFFFFF;
  margin-bottom: 1rem;
}
.footer__link:last-of-type {
  margin-bottom: 0;
}
.footer__logo {
  max-width: 20rem;
  margin: 0 auto 5rem auto;
}
@media (min-width: 1024px) {
  .footer__logo {
    margin-bottom: 0;
  }
}
.footer__heading {
  color: #84d9bc;
  text-align: left;
}
@media (min-width: 1024px) {
  .footer__heading {
    margin-bottom: 3rem;
  }
}
.footer__copyright {
  text-align: center;
  color: #FFFFFF;
  margin: 0 auto;
  border-top: 2px solid #84d9bc;
  padding: 1rem;
  max-width: 120rem;
  font-size: 1.2rem;
  text-transform: uppercase;
}